import Vue from 'vue'
import Vuex from 'vuex'
import music from './music/index.js'
import myInfo from './myInfo/index.js'
Vue.use(Vuex);
import {
	getMusicLyric
} from '@/api/itemMusic.js'
export default new Vuex.Store({
	state: {
		topValue:'',//
		playList: [], //
		playListIndex: 0, //0,index
		isPlay: false, //
		detailShow: false, //
		currentTime: 0, //
		duration: 0, //
		lyricList: {}, //
		token: '',
		myInfo: {
			id:'',
			name:'',
			follow:'',
			fans:'',
			src:"static/assets/avatar.png",
			level:''
		}, //
		myMusicList:[],//
	},
	getters: {},
	mutations: {
		//isPlay
		updateIsPlay: function(state, value) {
			state.isPlay = value;
		},
		//
		updateDetailShow: function(state) {
			state.detailShow = !state.detailShow
		},
		//
		updateCurrentTime: function(state, value) {
			state.currentTime = value;
		},
		//
		updateDuration: function(state, value) {
			state.duration = value;
		},
		//
		updatePlayList: function(state, value) {
			state.playList = value;
		},
		//
		pushPlayList: function(state, value) {
			state.playList.push(value);
		},
		//
		updatePlayListIndex: function(state, value) {
			state.playListIndex = value;
		},
		//
		updateLyricList: function(state, value) {
			state.lyricList = value;
		},
		//token
		updateToken: function(state, value) {
			state.token = value;
		},
		//
		updateMyInfo: function(state, value) {
			state.myInfo = value;
		},
		//
		updateMyMusicList:function(state,value){
			state.myMusicList = value;
		},
		//
		updateTopValue:function(state,value){
			state.topValue = value;
		}
	},
	actions: {
		//
		getLyric: async function(context, value) {
			let res = await getMusicLyric(value);
			context.commit("updateLyricList", res.data.lrc)
		},
	},
	mudules: {
		music,
		myInfo
	}
})
