import store from '../store/index'
const audio = {};
var music = uni.createInnerAudioContext()
var previousSrc = '' //
audio.playAudios = function(src) {
	if (music.paused) { //
		if (previousSrc === src) {
			music.play();
		} else {
			music.stop();
			music.src = src;
			previousSrc = src;
			music.play();
		}
		store.commit('updateIsPlay', false);
	} else { //
		if (previousSrc == src) {
			music.pause();
			store.commit('updateIsPlay', true);
		} else {
			music.stop();
			music.src = src;
			previousSrc = src;
			music.play();
		}
	}
	//duration
	// store.commit('updateDuration',music.duration);
}
audio.stopAudios = function() {
	music.stop()
}
audio.pauseAudios = function() {
	music.pause()
}
audio.goto = function(newTime){
	music.seek(newTime);
}
music.onPlay(() => { //
	console.log('');
	
	store.commit('updateIsPlay', true);
});
music.onPause(() => { //
	console.log('');
	store.commit('updateIsPlay', false);
});
music.onSeeked(()=>{//
	console.log('');
})

music.onEnded(() => { // 
	console.log('');
	store.commit('updateIsPlay', false);
});
music.onCanplay(() => {
	//undefined,
	if (typeof music.duration != 'undefined') {
		store.commit('updateDuration',music.duration);
	} else {
		setTimeout(() => {
			store.commit('updateDuration',music.duration);
		}, 1000)
	}
});
music.onError((e) => {
	console.log('');
	uni.showToast({
		title:'o()o',
		duration:2000
	})
		//
		let index = store.state.playListIndex;//
		let list = store.state.playList;//
		//
		if(index + 1 === list.length){
			index = 0;
		}else{
			index++;
		}
		store.commit('updatePlayListIndex',index);
		//
		let src = `https://music.163.com/song/media/outer/url?id=${list[index].id}.mp3`;
		setTimeout(()=>{
			audio.playAudios(src);
		},2000);
		
})
audio.music = music;
export default audio
